<template>
  <div>
    <component :is="currentHeader" />
  </div>
</template>

<script>
import AdminHeader from "@/components/admin_page/AdminHeader.vue";
import ClientHeader from "@/components/client_page/ClientHeader.vue";

export default {
  name: "Header",
  computed: {
    currentHeader() {
      const routeName = this.$route.name;
      if (routeName?.startsWith("Admin")) {
        return AdminHeader;
      } else {
        return ClientHeader;
      }
    },
  },
};
</script>
